@import "./global.scss";
.app {
  height: 100vh;

  @include mobile{
    
  }
  .sections {
    width: 100%;
    height: 100vh;
    position: relative;
    scroll-behavior: smooth;
    scroll-snap-type: y mandatory;
    scrollbar-width: none;
    &::-webkit-scrollbar {
      display: none;
    }

    > * {
      width: 100vw;
      height: 100vh;
      scroll-snap-align: start;
    }
  }
}
